import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgbCollapseModule, NgbDatepickerModule, NgbDropdown, NgbDropdownModule, NgbPaginationModule } from '@ng-bootstrap/ng-bootstrap';
import { UIModule } from 'src/app/shared/ui/ui.module';
import { EnquiryDetailsComponent } from './enquiry-details/enquiry-details.component';
import { SimplebarAngularModule } from 'simplebar-angular';
import { PickerModule } from '@ctrl/ngx-emoji-mart';
import { ListComponent } from './list/list.component';


const routes: Routes = [
{path:'enquiry-details',component:EnquiryDetailsComponent},
{path:'enquiries',component:ListComponent}
 ];
 export const enquiryListRouting = RouterModule.forChild(routes);

@NgModule({
  declarations: [
    EnquiryDetailsComponent,ListComponent
  ],
  providers:[NgbDropdown],
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    NgbDatepickerModule,
    NgbPaginationModule,
    NgbDropdownModule,
    NgbCollapseModule,
    UIModule,
    SimplebarAngularModule,
    PickerModule,
    enquiryListRouting
  ]
})
export class EnquiryListModule { }
